@font-face {
  font-family: 'fontello';
  src: url('@{fontsPrefix}plone-fontello.eot?44831494');
  src: url('@{fontsPrefix}plone-fontello.eot?44831494#iefix') format('embedded-opentype'),
       url('@{fontsPrefix}plone-fontello.woff?44831494') format('woff'),
       url('@{fontsPrefix}plone-fontello.ttf?44831494') format('truetype'),
       url('@{fontsPrefix}plone-fontello.svg?44831494#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
// Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. 
